@import url('https://fonts.googleapis.com/css2?family=Gabarito:wght@400;500;600;700;800;900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    width: 100%;
    height: 100%;
}

.App, #root{
  text-align: center;
    width: 100%;
    height: 100%;
}

body{
    width: 100%;
    height: 100%;
}

.app_body {
  background-color: #000000;
  /*min-height: 100vh;*/
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: start;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: Gabarito, sans-serif;
}

.page_header{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 4vh 48px;
}

.page_body{
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 48px;
    gap: 40px;
    width: 100%;
}

.form_div{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
}

img{
    object-fit: cover;
}

.prompt_input{
    width: 25vw;
    min-width: 160px;
    height: 320px;
    background-color: #e0e0e0;
    color: black;
    font-family: Gabarito, sans-serif;
    font-size: 18px;
    padding: 16px;
}

.generate_button{
    width: 25vw;
    height: 60px;
    background-color: #5B6A00;
    color: white;
    font-family: Gabarito, sans-serif;
    font-size: 18px;
    padding: 16px;
}

.instructions{
    font-weight: 400;
    font-size: 14px;
    max-width: 25vw;
    text-align: left;
}

.panel_img{
    height: 400px;
    width: 400px;
    min-width: 400px;
    /*object-fit: cover;*/
}

.page_footer{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: start;
    padding: 24px 32px;
    border-top: white 1px solid;
    /*overflow: hidden;*/
    overflow: auto;
}
.page_footer::-webkit-scrollbar {
    display: none;
}

.page_footer {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

.logo{
    width: 320px;
}

.download_div{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 20px 24px;
    border: white 1px solid;
    background-color: black;
}

.download_text{
    font-family: Gabarito, sans-serif;
    font-size: 20px;
    margin-right: 20px;
}

.download_img{
    width: 14px;
}

.download_div:hover{
    background-color: #2d2d2d;
    cursor: pointer;
}

.carousel_item, .add_item{
    height: 120px;
    width: 120px;
    min-width: 120px;
    /*border: white 0.5px solid;*/
    margin-right: 20px;
}

.carousel_item:hover, .add_item:hover{
    cursor: pointer;
    border: white 1px solid;
}

.add_item{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: white 1px dashed;
    /*background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='white' stroke-width='4' stroke-dasharray='6%2c 14' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");*/
}

.carousel_img{
    height: 100%;
    width: 100%;
}

.add_img{
    height: 28px;
    width: 28px;
}

.add_img:hover{
    cursor: pointer;
}


